@import '../../styles/scss/variables';
@import '../../styles/scss/iconfonts';


.container_main {
    position: relative;
    background-color: $blue;
    padding: 0 4rem;
    p {
        position: relative;
        z-index: 2;
    }
    h1 {
        position: relative;
        z-index: 2;  
    }
    a {
        position: relative;
        z-index: 2;  
    }
    .hero {
        padding: 6rem 0;
        display: flex;
        justify-content: flex-end;
        .hero_right {
            width: 75%;
            display: flex;
            gap: 2rem;
            position: relative;
            .text_right_content {
                width: 33.33%;
                height: 100%;
                justify-content: flex-end;
                display: flex;
                flex-direction: column;
                color: $light-blue;
                line-height: 160%;
                padding: 0 0 4rem 0;
                box-sizing: border-box;
                gap: 1rem;
            }
            .content_h1 {
                position: absolute;
                left: -34%;
                h1 {
                    width: 60%;
                    line-height: 120%;
                    margin: 5rem 0 2rem 0;
                    span {
                        position: relative;
                        &::after {
                            content: '';
                            position: absolute;
                            left: -45px;
                            top: 9px;
                            background-image: url(../../assets/images/circle2.png);
                            width: 310px;
                            height: 94px;
                            background-size: cover;
                        }
                    }
                }
            }
        }
    }
    .image_hero {
        width: 66.66%;
        height: max-content;
    }
    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-image: url(../../assets/images/bg-sketch.jpg);
        left: 0;
        top: 0;
        mix-blend-mode: multiply;
        opacity: 0.5;
        z-index: 1;
    }
}
